  --muted: 196 15% 85%;
  --muted-foreground: 210 10% 45%;
  --accent: 196 15% 85%;
  --accent-foreground: 210 25% 15%;
  --destructive: 0 84.2% 60.2%;
  --destructive-foreground: 60 100% 95%;
  --border: 196 20% 88%;
  --input: 196 20% 88%;
  --ring: 196 100% 31%;
  --radius: 0.75rem;
  --industrial-blue: 196 100% 31%;
  --premium-orange: 25 95% 53%;
  --premium-teal: 196 100% 31%;
